/* Checkout-CSS*/
#checkout {
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 100%;
    width: 100%;
}

#checkout div {
    padding: 8px;
    height: fit-content;
    width: 700px;
    margin-top: 20px;
    box-shadow: 0 4px 18px 10px rgba(0, 0, 0, 0.2);
    text-align: center;
}

#checkout div h1 {
    border-bottom: 2px black solid;
    padding: 8px;
    margin-bottom: 20px;
    text-align: center;

}

#checkout div button {
    padding: 8px;
    margin: 20px;
    border-radius: 2px;
}

#backToHome {
    background-color: #015582;
    color: #ffff;
    width: 160px;
}

#backToHome:hover {
    background-color: #459abe;
}

#checkout #proceedToCheckoutButton {
    background-color: #015582;
    color: #ffff;
    width: 100px;
}

#checkout #proceedToCheckoutButton:hover {
    background-color: #459abe;
}

#checkout #removeFromCartButton:hover {
    background-color: #cd0303;
}

#checkout #removeFromCartButton {
    background-color:#B30000;
    color: #ffff;
    width: 100px;
}

#continueToHomePageButton{
    width: 100px;
}

#checkout div section {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    width: 100%;
    height: 80px;
}

#checkout li {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
    align-items: center;
    border-bottom: 1px grey solid;
    height: 120px;
}

#cartLabels {
    height: 20px;
}

#checkout img {
    height: 100px;
    width: 100px;
    object-fit: scale-down;
}

#checkout span {
    width: 25%;
    text-align: center;
}

#checkout #totalSum {
    height: 50px;
    justify-content: flex-end;
}